<template>
  <div class="container">
    <div class="header">
      <div class="title">编辑代理商</div>
      <div class="back" @click="hanleHide">
        <fa-icon icon-name="undo" />
        返回
      </div>
    </div>
    <el-divider />
    <div class="wrap">
      <el-row>
        <el-col :lg="16" :md="20" :sm="24" :xs="24">
          <el-form ref="addForm" :model="form" status-icon label-width="100px" @submit.native.prevent>
            <el-form-item label="公司名" prop="name">
              <el-input v-model="form.company_name" size="medium" placeholder="请填写公司名"/>
            </el-form-item>
            <el-form-item label="用户名" prop="username">
              <el-input v-model="form.username" size="medium" placeholder="请填写用户名"/>
            </el-form-item>
            <el-form-item label="真实姓名" prop="realname">
              <el-input v-model="form.realname" size="medium" placeholder="请填写真实姓名"/>
            </el-form-item>
            <el-form-item label="手机号" prop="mobile">
              <el-input v-model="form.mobile" size="medium" placeholder="请填写手机号"/>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="form.email" size="medium" placeholder="请填写邮箱"/>
            </el-form-item>
            <el-form-item class="submit">
              <el-button type="primary" @click="submitForm('addForm')">保 存</el-button>
              <el-button @click="resetForm('addForm')">重 置</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import AgentModel from '@/models/agent'
export default {
  name: 'AgentEdit',
  props: {
    editID: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      form: {
        company_name: '',
        email: '',
        mobile: '',
        realname: '',
        username: ''
      }
    }
  },
  mounted() {
    this.getAgent()
  },
  methods: {
    async getAgent() {
      const res = await AgentModel.getOne(this.editID)
      this.form.company_name = res.company_name
      this.form.email = res.email
      this.form.mobile = res.mobile
      this.form.realname = res.realname
      this.form.username = res.username
    },
    hanleHide() {
      this.$emit('hanleHide', false)
    },
    async submitForm() {
      const res = await AgentModel.editOne(this.editID, this.form)
      this.$message.success('修改代理商信息成功')
      this.hanleHide()
    },
    // 重置表单
    resetForm(formName) {
      this.getAgent()
    },
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .el-divider--horizontal {
    margin: 0;
  }
  .container {
  height: 100%
  .header {
  display flex
  justify-content space-between
  align-items center
  .title {
    height 59px
    line-height 59px
    font-size 16px
    font-weight 500
  }
  .back {
    margin-right 40px
    cursor pointer
  }
  }
  .wrap {
    padding: 20px;
  }
  }
</style>
