/**
 * 公司模块
 */

import {
  post,
  get,
  put,
  _delete
} from '@/utils/request'

export default class Agent {
  /**
   *
   * @param {*} size 每页条(默认每页10条数据)
   * @param {*} page 第几页(默认获取第1页的数据；对于数据库而言是第0页)
   */
  static async getList(size, page) {
    const data = await get(`cms/agent/list?page=${page}&size=${size}`)
    return data
  }

  // 新增代理商
  static async createOne(agent) {
    const data = await post('cms/agent', agent)
    return data
  }

  // 获取代理商信息
  static async getOne(id) {
    const data = await get(`cms/agent/${id}`)
    return data
  }

  // 修改代理商信息
  static async editOne(id, agent) {
    const data = await put(`cms/agent/${id}`, agent)
    return data
  }

  // 删除代理商
  static async deleteOne(id) {
    const data = await _delete(`cms/agent/${id}`)
    return data
  }
}
