<template>
  <div>
    <div v-if="!showEdit && !showAdd" class="container">
      <div class="header">
        <div class="title">公司列表</div>
        <el-button style="margin-left: 30px" @click="showAdd=!showAdd">添加代理商</el-button>
      </div>
      <ceres-table
        :pagination="pagination"
        :table-column="tableColumn"
        :table-data="tableData"
        :operate="operate"
        @handleEdit="handleEdit"
        @handleDelete="handleDelete"
        @currentChange = "currentChange"
      />
    </div>

    <agent-add v-if="showAdd" @hanleHide="handleHideAdd"/>
    <agent-edit v-if="showEdit" :edit-i-d="editID" @hanleHide="handleHideEdit"/>
  </div>
</template>

<script type="text/ecmascript-6">
import AgentModel from '@/models/agent'
import AgentAdd from './AgentAdd'
import AgentEdit from './AgentEdit'

export default {
  name: 'CompanyList',
  components: {
    AgentAdd,
    AgentEdit
  },
  data() {
    return {
      tableColumn: [
        { prop: 'username', label: '用户名' },
        { prop: 'auth_scope', label: '权限' },
        { prop: 'company_name', label: '公司名' },
        { prop: 'mobile', label: '手机号' },
        { prop: 'email', label: '邮箱' },
      ],
      tableData: [],
      operate: [
        { name: '编辑', func: 'handleEdit', type: 'primary' },
        { name: '删除', func: 'handleDelete', type: 'danger' },
      ],
      showEdit: false,
      showAdd: false,
      pagination: {
        pageSize: 0,
        pageTotal: 0
      },
      editID: ''
    }
  },
  computed: {},
  created() { },
  mounted() {
    this.getAgentList(1, 10)
  },
  methods: {
    async getAgentList(page, size) {
      const res = await AgentModel.getList(size, page)
      this.tableData = [...res.items]
      this.pagination.pageTotal = res.total
      this.pagination.pageSize = res.size
    },
    handleEdit(val) {
      this.showEdit = true
      this.editID = val.row.id
    },
    async handleDelete(val) {
      const res = await AgentModel.deleteOne(val.row.id)
      this.$message.success('删除成功')
      this.getAgentList(1, 10)
    },
    handleHideAdd() {
      this.showAdd = false
      this.getAgentList(1, 10)
    },
    handleHideEdit() {
      this.showEdit = false
      this.getAgentList(1, 10)
    },
    // 切换当前页
    currentChange(page) {
      this.getAgentList(page, 10)
    }
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .container {
    .header {
      display flex
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
    }
  }
</style>
